.container {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  gap: 1.5rem;

  .title {
    color: #2a8b53;
    font-weight: bold;
    font-size: 1.75rem;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
  }
  
  .note {
    text-align: center;
  }

  .clock {
    color: #2a8b53;
    font-weight: bold;
    font-size: 1.25rem;
    text-align: center;
    margin: 0;
  }

  .box {
    border: 1px solid rgba(0,0,0,.125);
    padding: 1rem;
    text-align: center;
    border-radius: 0.25rem;

    &:hover {
      box-shadow: 0 1rem 3rem rgba(130, 140, 125, 0.5);
    }

    .image {
      border-radius: 50%;
      width: 10rem;
      height: 10rem;
      overflow: hidden;
      margin: auto;

      > img {
        width: 100%;
      }
    }
  
    .name {
      font-weight: bold;
      font-size: 1.25rem;
      color: black;
    }
  }

}
