.container {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  gap: 1.5rem;

  .title {
    color: #2a8b53;
    font-weight: bold;
    font-size: 1.75rem;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
  }
  
  .note {
    text-align: center;
  }

  .map {
    width: 100%;
    height: 500px;
  }

}

.button {
  background-color: #d5a66d;
  border-color: #d5a66d;
  text-align: center;
  vertical-align: middle;
  display: inline-block;
  font-weight: 400;
  user-select: none;
  border: 1px solid transparent;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 1.5;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  color: #fff;
  cursor: pointer;
  border-radius: 0.25rem;

  &:hover {
    background-color: #d5a66d;
    border-color: #d5a66d;
    box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175);
  }
}