.container {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column;
  gap: 1.5rem;

  .title {
    color: #2a8b53;
    font-weight: bold;
    font-size: 1.75rem;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
  }
  
  .note {
    text-align: center;
  }

}
