.app {
  width: 100vw;
  min-height: 100vh;
  position: relative;
  background-color: white;

  @media (min-width: 450px) {
    max-width: 450px;
  }
}

.firstPage {
  min-height: max(100vh, 50rem);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  isolation: isolate;

  .center {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    gap: 1rem;
    padding-left: 1rem;
    padding-right: 1rem;
    z-index: 1;
    margin-bottom: 10rem;
    
    .hero {
      width: 100%;
    }

    .textContainer {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-flow: column;
      gap: 1rem;
      margin-bottom: 2rem;
      text-align: center;
      transition: opacity 300ms ease-in-out;

      .text {
        font-size: 1.25rem;
        font-weight: bold;
      }

      .greenText {
        font-size: 1.25rem;
        font-weight: bold;
        color: #2a8b53;
        text-transform: capitalize;
      }

      .nameText {
        font-family: 'Cookie', cursive;
        font-size: 3.75rem;
        font-weight: bold;
        color: #2a8b53;
      }
    }

    .button {
      margin-bottom: 6rem;
    }
  }

  .bawah {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: -1;
  }
}

.kiri {
  position: absolute;
  z-index: 0;
  width: 300px;
  left: 0;
  top: 0;
}

.kanan {
  position: absolute;
  z-index: 0;
  width: 300px;
  right: 0;
  top: 0;
}

.button {
  opacity: 1;
  background-color: #d5a66d;
  border-color: #d5a66d;
  text-align: center;
  vertical-align: middle;
  display: inline-block;
  font-weight: 400;
  user-select: none;
  border: 1px solid transparent;
  padding: .375rem .75rem;
  font-size: 1rem;
  line-height: 1.5;
  transition: opacity 300ms ease-in-out, color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  color: #fff;
  cursor: pointer;
  border-radius: 0.25rem;

  &:hover {
    background-color: #d5a66d;
    border-color: #d5a66d;
    box-shadow: 0 1rem 3rem rgba(0, 0, 0, 0.175);
  }
}

.willLeave {
  pointer-events: none;
  opacity: 0 !important;
}

.mount {
  animation-duration: 300ms;
  animation-name: mount;
}

@keyframes mount {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}